export const checkNum = (rule, value, callback) => {
  if (!value) {
    return callback(new Error('请输入时间'));
  }
  this.checkTimer = setTimeout(() => {
    if (!Number.isInteger(value)) {
      callback(new Error('请输入整数'));
    } else if (value > 999) {
      callback(new Error('请输入3位以内的数字'));
    } else {
      callback();
    }
  }, 500);
  return true;
};

// 清空前后空格
export const spaceBlur = (value) => {
  const notspaceReg = /(^\s*)|(\s*$)/g;
  return value.replace(notspaceReg, '');
};

// 判断是否是IE浏览器
export const testIE = () => {
  // 取得浏览器的userAgent字符串
  const userAgent = navigator.userAgent;
  return (userAgent.indexOf('compatible') > -1 &&
            userAgent.indexOf('MSIE') > -1 &&
            userAgent.indexOf('Opera') < 0);
};



