<template>
  <div>
    <div class="c_title">

      <div class="c_title_area" @click="onBack">
        <img src="../assets/back.png"/>
      </div>
      <span>新增报送信息</span>

    </div>


    <div class="c_form_div">

      <el-form class="c_form" ref="publishForm" :model="publishForm"
               :rules="rules" label-width="80px">
        <el-form-item label="投稿标题">
          <el-input v-model="publishForm.title" @blur="spaceName('title',publishForm.title)"
                    placeholder="请填写投稿标题"
          ></el-input>
        </el-form-item>
        <el-form-item label="创建人" prop="userStr" class="write-area">
          <div @click.stop="userStrFn">
            <el-input v-model="publishForm.userStr"
                      placeholder="请选择"
                      suffix-icon="el-icon-caret-bottom drop-down-icon"
                      :editable="false" readonly></el-input>
          </div>
        </el-form-item>
        <el-form-item label="供稿部门" prop="deptStr" class="write-area">
          <div @click.stop="deptStrFn">
            <el-input v-model="publishForm.deptStr"
                      placeholder="请选择"
                      suffix-icon="el-icon-caret-bottom drop-down-icon"
                      :editable="false" readonly></el-input>
          </div>
        </el-form-item>

        <el-form-item label="稿件分类">

          <el-select v-model="publishForm.typeStr"
                     size="mini"
                     placeholder="请选择" class="c_classfic_select">
            <el-option

              v-for="item in classficOptions"
              :key="item.id"
              :label="item.draftName"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="稿件栏目">

          <el-select v-model="publishForm.columnStr"
                     size="mini"
                     placeholder="请选择" class="c_column_select">
            <el-option

              v-for="item in  columnOptions"
              :key="item.id"
              :label="item.draftName"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>


        <el-form-item label="稿件内容">
          <el-input type="textarea"
                    :rows="6"
                    v-model="publishForm.content"></el-input>
        </el-form-item>
        <el-form-item label="上传附件">
          <el-button  class="c_button_upload" size="mini" type="primary" >点击上传</el-button>
          <span>支持xxxx</span>
        </el-form-item>
      </el-form>

    </div>


    <div class="c_footer">

      <div class="c_bottom_button">
          <el-button  size="small" class="c_button_submit" type="primary"
                      @click="onSubmit">提交</el-button>
          <el-button size="small" class="c_button_reset">重置</el-button>

      </div>
    </div>

    <EimTree class="pb-tree" ref="tree"/>


  </div>
</template>

<script>
import EimTree from '@/common/vendor/tree/Tree';
import * as validate from '@/common/js/validate';
import * as utils from '@/common/js/utils';
import * as constants from '@/common/js/constants';

export default {
  name: 'new_info',
  components: {
    EimTree,
  },
  data() {
    return {
      classficOptions: [],
      columnOptions: [],
      publishForm: {
        title: '', // 刊物名称
        userStr: '', // 创建人
        userArr: [],
        deptStr: '', // 供稿部门
        deptArr: [],
        pages: '', // 期次/页码
        files: [], // 上传附件
        typeStr: '',
        columnStr: '',
        content: '',
      },
      rules: {
        title: [
          { required: true, message: '请填写投稿标题', trigger: 'blur' },
          { min: 1, max: 30, message: '最大长度在30个字符', trigger: 'blur' },
        ],
        deptStr: [
          { required: true, message: '请选择供稿部门', trigger: 'change' },
          { min: 1, message: '请选择供稿部门', trigger: 'blur' },
        ],
        userStr: [
          { required: true, message: '请选择创建人', trigger: 'change' },
          { min: 1, message: '请选择创建人', trigger: 'blur' },
        ],
        typeStr: [
          { required: true, message: '请选择稿件分类', trigger: 'blur' },
          { min: 1, max: 30, message: '最大长度在30个字符', trigger: 'blur' },
        ],
        columnStr: [
          { required: true, message: '请选择稿件栏目', trigger: 'blur' },
          { min: 1, max: 30, message: '最大长度在30个字符', trigger: 'blur' },
        ],
        files: [
          { required: true, message: '请上传附件', trigger: 'blur' },
          { min: 1, max: 30, message: '最大长度在30个字符', trigger: 'blur' },
        ],
      },


    };
  },
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    spaceName(name, value) {
      this.publishForm[name] = validate.spaceBlur(value);
    },
    userStrFn() {
      const url = constants.selectUserUrl;
      const tree = this.$refs.tree;
      const person = this.publishForm.userArr;
      utils.selectPerson(url, tree, person, (res) => {
        this.publishForm.userStr = utils.getOrgName(res, 'name');// 获取名字
        console.log(res);
      }, 3);
    },
    deptStrFn() {
      const url = constants.selectOrgUrl;
      const tree = this.$refs.tree;
      const depart = this.publishForm.deptArr;
      utils.selectOrg(url, tree, depart, (org) => {
        this.publishForm.deptStr = utils.getOrgName(org, 'name');
        console.log(org);
      }, 2);
    },
    onSubmit() {

    },
  },
};
</script>

<style scoped>

  .c_title {

    background: #0D6BD6;
    height: 48px;
    text-align: center;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 100;

  }

  .c_title_area {

    width: 60px;
    height: 100%;
    float: left;

  }

  .c_title_area:hover {

    background-color: #0a5fd0;

  }

  .c_title img {

    width: 24px;
    height: 24px;
    margin-top: 12px;

  }

  .c_title span {

    font-size: 16px;
    color: #FFFFFF;
    letter-spacing: 0;
    margin-top: 16px;
    text-align: center;
    display: inline-block;
    margin-left: -60px;

  }

  .c_form_div {

    position: relative;
    margin-top: 48px;

  }

  .c_form {

    width: 420px;
    position: absolute;
    left: 50%;
    margin-left: -210px;
    padding-top: 20px;
    height: 100%;

  }

  .c_classfic_select {
    width: 340px;
  }

  .c_column_select {
    width: 340px;
  }

  .c_bottom_button {

    margin-left: -48px;
    text-align: center;
    height: 100%;

  }

  .c_footer {


    position: fixed;
    bottom: 0;
    z-index:100;

    background: #F5F7FD;
    /*box-shadow: inset 0 1px 0 0 #EBEEF6;*/
    width: 100%;
    height: 50px;
    vertical-align: center;
  }

  .c_button_submit{

    width: 94px;

    margin-top: 10px;

  }

  .c_button_reset{

    width: 94px;
    margin-top: 10px;
    margin-left: 20px;

  }

  .c_button_upload{
    width: 68px ;
    height: 28px ;
    padding: 0;
  }

</style>



