<template>
  <div class="c_content_g c_content">

    <div class="c_content_top">

      <span>时间段</span>
      <el-date-picker
        v-model="date_value"
        class="c_el-date-picker"
        type="daterange"
        size="mini"
        @change ='onDateChange'
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
      <span>稿件分类</span>
      <el-select v-model="classfic_value"
                 size="mini"
                 @change ='onClassficChange'
                 placeholder="全部" class="c_classfic_select">
        <el-option

          v-for="item in classficOptions"
          :key="item.id"
          :label="item.draftName"
          :value="item.id">
        </el-option>
      </el-select>
      <span>栏目</span>
      <el-select v-model="column_value"
                 size="mini"
                  @change ='onColumnChange'
                 placeholder="全部" class="c_column_select">
        <el-option

          v-for="item in  columnOptions"
          :key="item.id"
          :label="item.draftName"
          :value="item.id">
        </el-option>
      </el-select>

      <el-button @click="onAddClick" size='mini' type="success" class="c_button_add">
        +新增报送信息
      </el-button>


    </div>


    <div>
      <el-table class="c_table_base c_table_info"
                :data="tableData"
                @row-click="onTableRowClick"

      >
        <el-table-column
          prop="title"
          label="主题"
          min-width="40%"
          >
        </el-table-column>
        <el-table-column class="c_el-table-column"
                         prop="draftTypeStr"
                         label="稿件分类"
                         min-width="10%"
        >
        </el-table-column>
        <el-table-column class="c_el-table-column"
                         prop="draftColumnStr"
                         min-width="10%"
                         label="栏目">
        </el-table-column>

        <el-table-column
          prop="statusStr"
          label="状态"
          min-width="10%"

        >
          <template slot-scope="scope">
            <el-tag

              size="mini"
              :type="stausBackguand(scope.row.status)"
              disable-transitions>{{scope.row.statusStr}}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column class="c_el-table-column"
                         prop="createTime"
                         min-width="10%"

                         :formatter='updateTimeFormat'
                         label="提交时间">
        </el-table-column>
      </el-table>


    </div>


    <el-pagination

      class="c_pagination"
      layout="prev, pager, next"
      :total="dataTotal">
    </el-pagination>

  </div>
</template>

<script>

import Api from '@/api/api';

export default {
  name: 'send_msg',
  data() {
    return {
      tableData: [],
      classficOptions: [],
      columnOptions: [],
      classfic_value: '',
      column_value: '',
      date_value: [],
      dataTotal: 0,
      pageNo: 1,

    };
  },
  mounted() {
    this.getData(this.generateParam());
    Api.getClassficData((data) => {
      console.log(data);
      this.classficOptions = data;
    });
    Api.getColumnData((data) => {
      console.log(data);
      this.columnOptions = data;
    });
  },
  methods: {
    onAddClick() {
      this.$router.push({
        name: 'newInfo',
      });
    },
    stausBackguand(status) {
      let result;
      switch (status) {
        case 4:
          result = 'primary';
          break;
        case 2:
          result = 'success';
          break;
        case 3:
          result = 'danger';
          break;
        default:
          break;
      }
      return result;
    },
    // 获取table数据
    getData(param) {
      if (typeof param !== 'object') {
        param = {};
      }

      if (!param.hasOwnProperty('pageSize')) {
        param.pageSize = '10';
      }

      Api.getApi('get', 'pageQueryAlreadySubmitInfo', param, (data) => {
        console.log(data);

        this.tableData = data.records;
        this.dataTotal = data.size;
      }, (data) => {
        console.log(data);
      });
    },
    updateTimeFormat(row, column, cellValue) {
      if (typeof cellValue === 'string') {
        return cellValue.split(' ')[0];
      }
      return '';
    },
    onTableRowClick(row, event, column) {
      this.$router.push({
        name: 'infoDetail',
        params: row,
      });
    },
    onDateChange() {
      this.getData(this.generateParam());
    },
    onClassficChange() {
      this.getData(this.generateParam());
    },
    onColumnChange() {
      this.getData(this.generateParam());
    },
    generateParam() {
      console.log(this.date_value);

      return {
        startTime: this.date_value !== null && this.date_value.length > 0 ? this.getDateFormartStr(this.date_value[0]) : null,
        endTime: this.date_value !== null && this.date_value.length > 0 ? this.getDateFormartStr(this.date_value[1]) : null,
        deaftTypeId: this.classfic_value,
        deaftColumnId: this.column_value,
        pageNo: this.pageNo,
        pageSize: '10',
      };
    },
    getDateFormartStr(date) {
      if (date) {
        return `${date.getFullYear()}-${('0' + (date.getMonth() + 1)).slice(-2)}-${('0' + date.getDate()).slice(-2)}`;
      }
      return '';
    },
  },
};
</script>

<style scoped>

  .c_content {

    padding: 8px 20px 10px 12px;


  }

  .c_content_top {
    background: #F5F7FA;
    border-radius: 2px;
    padding: 8px 10px;
  }

  .c_table_base {

    border: 1px solid lightgray;
  }

  .c_table_info {

    margin-top: 12px;
  }

  .c_el-date-picker {

    width: 244px !important;
    margin-left: 1%;
    margin-right: 3%;
  }

  .c_classfic_select {

    width: 124px !important;
    margin-left: 1%;
    margin-right: 3%;

  }

  .c_column_select {

    width: 124px !important;
    margin-left: 1%;

  }

  .c_content_top span {
    font-size: 14px;
    color: #606266;
    letter-spacing: 0;
    line-height: 14px;
  }

  .c_button_add {
    position: absolute;
    right: 3%;
  }

  .c_pagination {
    position: absolute;
    right: 20px;
    margin-top: 30px;
  }

  .el-pagination {

    color: #606266 !important;
  }

  .el-table th {

    background-color: #EBEEF6 !important;
  }

  .el-button--success {
    color: #fff;
    background-color: #29bf6c;
    border-color: #29bf6c;
  }

  .el-button--success:hover {
    color: #fff;
    background-color: #15b300;
    border-color: #15b300;
  }


</style>



