<template>

  <div class="c_score_rank">
    <ul>
      <li v-for="rankInfo in scoreRankList" class="c_rank_item">

        <div :class="rankInfo.class">
          <span>{{rankInfo.No}}</span><span>{{rankInfo.department}}</span><span>{{rankInfo.score}}</span>
        </div>

      </li>
    </ul>

  </div>

</template>

<script>


export default {
  name: 'score-rank',
  components: {},
  data() {
    return {

      scoreRankList: [{

        No: 1,
        department: '法律部',
        score: 800,
        class: 'c_rank_class_1',


      }, {

        No: 2,
        department: '法律部2',
        score: 700,
        class: 'c_rank_class_2',

      }, {

        No: 3,
        department: '法律部3',
        score: 600,
        class: 'c_rank_class_3',

      }, {

        No: 4,
        department: '法律部4',
        score: 500,
        class: 'c_rank_class_4',

      },


      ],

    };
  },

};
</script>

<style scoped>

  .c_score_rank {

    border: 1px solid lightgray;

  }

  .c_rank_item {

    line-height: 40px;
  }

  ul {

    position: relative;

    width: 210px;

  }

  .c_rank_class_1 {

    position: relative;

  }

  .c_rank_class_2 {

    position: relative;

  }

  .c_rank_class_3 {

    position: relative;

  }

  .c_rank_class_1 span:nth-child(1), .c_rank_class_2 span:nth-child(1),
  .c_rank_class_3 span:nth-child(1) {

    width: 24px;
    height: 26px;
    margin-left: 9px;

    background-repeat: no-repeat;
    background-position: center;
    background-color: transparent;

  }

  .c_rank_class_1 span:nth-child(1) {

    background-image: url('../assets/rankings_1.png');

  }

  .c_rank_class_2 span:nth-child(1) {

    background-image: url('../assets/rankings_2.png');

  }

  .c_rank_class_3 span:nth-child(1) {

    background-image: url('../assets/rankings_3.png');

  }

  .c_rank_class_1 span:nth-child(3), .c_rank_class_2 span:nth-child(3),
  .c_rank_class_3 span:nth-child(3) {

    font-size: 14px;
    color: #FF6D50;

  }

  .c_rank_class_4 {
    position: relative;

  }

  span:first-child {

    margin-left: 12px;
    width: 18px;
    height: 18px;
    color: #ffffff;
    background: #A4BBC4;

    font-size: 12px;
    text-align: center;
    display: inline-block;
    line-height: 18px;
    vertical-align: 2px;
    border-radius: 200px;

  }

  span:nth-child(2) {

    margin-left: 8px;
    font-size: 13px;
    color: #333333;
    letter-spacing: 0;
    position: absolute;

  }

  span:nth-child(3) {

    position: absolute;
    top: 0;
    right: 20px;
    font-size: 14px;
    color: #808080;
    letter-spacing: 0;
  }

</style>



