<template>

  <div>

    <div class="c_title">

      <div class="c_title_area" @click="onBack">
        <img src="../assets/back.png"/>
      </div>
      <span>信息详情</span>

    </div>
    <div class="c_content_g c_parent">
      <span class="c_title2">{{$route.params.title}}</span>
      <span class="c_classfic" v-if="$route.params.draftTypeStr">
        稿件分类：{{$route.params.draftTypeStr}}</span>
      <span class="c_column" v-if="$route.params.draftColumnStr">
        稿件栏目：{{$route.params.draftColumnStr}}</span>
      <span class="c_time">发表时间：{{$route.params.updateTime}}</span>
      <p class="c_p">{{$route.params.draftContent}}</p>
      <label class="c_author" v-if="$route.params.subUsers">(作者：{{$route.params.subUsers}}<span
        style="display:inline-block;padding-left:20px;">

      </span>供稿部门：{{$route.params.subDepts}})</label>

      <div>
        <span class="c_file_span" v-if="$route.params.files && $route.params.files.size >0">
          附件下载</span>

        <ul v-if="$route.params.files && $route.params.files.size >0">

          <li v-for="file in $route.params.files" :key="file.filename">

            <img src="../assets/word.png" class="c_file_icon" />
            <span class="c_file_name">{{file.filename}}</span>
            <span class="c_file_size">{{file.filesize}}</span>
            <el-button class="c_btn_download" type="text">下载</el-button>
          </li>

        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'info_detail',
  data() {
    return {
      art: {
        title: '龙岗供电局全力做好城中村配变安全整治工作',
        classfic: '综合类信息',
        column: '安全生产',
        time: '2017-03-24',
        content:
          '      为配合政府产业布局调整，更好地服务当地经济社会发展，满足广大用电客户的需求，近期龙岗供电局启动组织机构优化调整,挂牌成立龙城布吉、横岗、平湖、坪地五个供电分局。\n' +
          '      五个供电分局主要负责地方电网规划建设、配电运行维护及中低压客户的供电服务工作，标志着龙岗区电力发展迈入新的阶段。\n' +
          '      龙岗供电局组织机构调整后将继续秉承“主动承担社会责任，全力做好电力供应”的企业理念，紧紧依托龙岗区政府与地方街道办，加强政企协同，服务深圳东进战略，为把龙岗区打造成东部中心提供坚强的电力保障和优质的供电服务。一是加强电网建设，推动电网建设项目的落地实施，不断完善配网网架，解决配变重过载问题，使居民用电更加安全可靠。二是保障电网安全运行，积极配合龙岗区政府推进城中村电力改造与电缆沟低压电缆治理，以设备为中心，做好运维管理，保障电网正常运行。三是全面提升服务能力，加大力度推进配网自动化和光纤通信全覆盖，低压集抄和智能电表全覆盖，大力推广用电业务同城办理、短信营业厅、95598业务受理和电费电子票据等服务，积极拓展网上营业厅、电话、短信等业务受理渠道，为辖区的企业、民生提供更加优质的供电服务。' +
          '',
        author: 'wuming',
        department: '移动互联网',
        doc: [
          {
            doctype: 'doc',
            filename: '本议题设计的单项合同估价金额.doc',
            filesize: '2.4m',
            fileUrl: '',
          },
          {
            doctype: 'xls',
            filename: '本议题设计的单项合同估价金额22.xls',
            filesize: '2.4m',
            fileUrl: '',
          },
          {
            doctype: 'ppt',
            filename: '本议题设计的单项合同估价33.ppt',
            filesize: '2.4m',
            fileUrl: '',
          },
        ],
      },
    };
  },
  mounted() {},
  methods: {
    onBack() {
      this.$router.go(-1);
    },
  },
};
</script>

<style scoped>
.c_title {
  background: #0d6bd6;
  height: 48px;
  text-align: center;
}

.c_title_area {
  width: 60px;
  height: 100%;
  float: left;
}

.c_title_area:hover {
  background-color: #0a5fd0;
}

.c_title img {
  width: 24px;
  height: 24px;
  margin-top: 12px;
}

.c_title span {
  font-size: 16px;
  color: #ffffff;
  letter-spacing: 0;
  margin-top: 16px;
  text-align: center;
  display: inline-block;
  margin-left: -60px;
}

.c_parent {
  padding: 23px 20px 10px 12px;
}

.c_title2 {
  text-align: center;
  display: block;
  margin-bottom: 14px;
}

.c_classfic {
  font-size: 13px;
  color: #909398;
  letter-spacing: 0;
  line-height: 13px;
}

.c_column {
  font-size: 13px;
  color: #909398;
  letter-spacing: 0;
  line-height: 13px;
  margin-left: 48px;
}

.c_time {
  position: absolute;
  right: 20px;

  font-size: 13px;
  color: #909398;
  letter-spacing: 0;
  line-height: 13px;
}

.c_author {
  font-size: 13px;
  color: #909398;
  letter-spacing: 0;
  line-height: 13px;
  margin-top: 12px;
}

.c_p {
  font-size: 15px;
  color: #303133;
  letter-spacing: 0;
  line-height: 23px;

  display: block;
  margin-bottom: 12px;
  margin-top: 24px;
  text-indent: 2em;
  text-justify: inter-ideograph;
}

.c_file_span {
  display: block;
  font-size: 14px;
  color: #737b8a;
  letter-spacing: 0;
  line-height: 14px;
  margin-top: 40px;
}

.c_file_icon {
  width: 28px;
  height: 28px;

  vertical-align: middle;
  text-align: center;
}

.c_file_name {
  font-size: 14px;
  color: #333333;
  letter-spacing: 0;
  line-height: 14px;
  margin-left: 10px;
}

.c_file_size {
  font-size: 14px;
  color: #737b8a;
  letter-spacing: 0;
  line-height: 14px;
  margin-left: 10px;
}

.c_btn_download {
  margin-left: 25px;
}

ul {
  border-top: 1px solid #ebebeb;
  margin-top: 5px;
}

li {
  line-height: 40px;
  vertical-align: middle;
}
</style>



