<template>
  <div class="c_content_g c_content">

    <el-table class="c_table_base c_table_info"
              :data="tableData"
              @row-click="onTableRowClick"
    >
      <el-table-column
        prop="policyName"
        label="主题"
        min-width="85%"
      >
      </el-table-column>

      <el-table-column class="c_el-table-column"
                       prop="createTime"
                       min-width="15%"
                       align="center"
                       :formatter= 'updateTimeFormat'
                       label="发表时间">
      </el-table-column>
    </el-table>

  </div>
</template>

<script>

import Api from '@/api/api';

export default {
  name: 'publish',
  data() {
    return {
      tableData: [],
      dataTotal: 0,
    };
  },
  methods: {
    // 获取table数据
    getData(param) {
      let temp = param;
      if (typeof temp !== 'object') {
        temp = {};
      }

      if (!temp.hasOwnProperty('pageSize')) {
        temp.pageSize = '10';
      }

      Api.getApi('get', 'queryRolePolicy', temp, (data) => {
        console.log(data);

        this.tableData = data.records;
        this.dataTotal = data.size;
      }, (data) => {
        console.log(data);
      });
    },

    updateTimeFormat(row, column, cellValue) {
      if (typeof cellValue === 'string') {
        return cellValue.split(' ')[0];
      }
      return '';
    },
    onTableRowClick(row) {
      this.$router.push({
        name: 'infoDetail',
        params: {
          title: row.policyName,
          draftContent: row.content,
          updateTime: row.createTime,
        },
      });
    },
  },
  mounted() {
    this.getData({});
  },
};
</script>

<style scoped>


  .c_content {

    padding: 8px 20px 10px 12px;

  }

  .c_table_base {

    border: 1px solid lightgray;
  }

  .c_table_info {

    margin-top: 12px;
  }

</style>



