// 选人组件
/*
* url 请求路径
* tree 组件引用节点
* person 选人数组
* maxNum 最大选人值
* depart 选部门数组
* callback 成功回调
*/
// 已选部门
function selectedFn(depart) {
  const arr = [];
  if (Array.isArray(depart) && depart.length) {
    depart.forEach((item) => {
      arr.push({
        id: item.orgId,
        name: item.name,
      });
    });
  }
  return arr;
}

export const selectPerson = (url, tree, person, callback, maxNum) => {
  const selected = [];
  let personData = null;

  // 已选
  if (Array.isArray(person) && person.length) {
    person.forEach((item) => {
      selected.push({
        id: item.userId,
        name: item.name,
      });
    });
  }
  personData = {
    type: 'user',
    orgModel: ['eim'],
    selected,
    dataType: 'orgUser',
    requserurl: url,
    // maxNum: 10,
    confirmCallback(data) {
      person.length = 0;
      data.forEach((v) => {
        person.push({
          name: v.name || '',
          userId: v.id || '',
        });
      });
      if (typeof callback === 'function') { callback(person); }
    },
  };
  // 如果传了最大值，默认全选
  if (maxNum) {
    personData.maxNum = maxNum;
  }
  tree.show(personData);
};
// 选择部门
export const selectOrg = (url, tree, depart, callback, maxNum) => {
  // 已选部门
  const selected = selectedFn(depart);
  let treeData = null;
  treeData = {
    type: 'org',
    orgModel: ['eim'],
    selected,
    reqorgurl: url,
    // maxNum,
    confirmCallback(result) {
      depart.length = 0; // 重置
      result.forEach((v) => {
        depart.push({
          name: v.name || '',
          orgId: v.id || '',
        });
      });
      if (typeof callback === 'function') { callback(depart); }
      // console.log(depart);
    },
  };
  // 如果传了最大值，默认全选
  if (maxNum) {
    treeData.maxNum = maxNum;
  }
  tree.show(treeData);
};
export const getOrgName = (org, key) => {
  let str = '';
  if ((key === 'name' || key === 'orgId') && Array.isArray(org)) {
    str = org.reduce((prev, cur) => {
      return prev + cur[key] + ', ';
    }, '');
    str = str.replace(/,\s*$/g, '') || '';
  }
  return str;
};



