<template>
  <div class="eim-tree" @click="searchblur">
    <el-dialog
      title="选择组织或人员"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      width="690px"
      :lock-scroll="true">
      <div class="eim-tree-content">
        <div class="eim-tree-left">
          <div class="eim-tree-search">
            <div class="search-input">
              <input v-model="searchInput" @click.stop="searchInputClick"
              @keyup.13="selectSearchResult"  @keyup.40="arrowDown"
              @keyup.38="arrowUp" placeholder="请输入搜索关键字">
              <i class="search-icon"></i>
            </div>
            <ul class="results" v-show="isShowResults && searchResults.length > 0">
              <li v-for="(r, $index) in searchResults"
              :key="$index" :class="{'bt': searchIndex === $index}"
              @click="handleSelect(r)">
              <span style="color: #333;">{{r.name}}</span> <span>({{r.value}})</span></li>
            </ul>
            <div class="results-nodata"
            v-show="isShowResults && searchResults.length === 0 && !!searchInput">暂无数据</div>
          </div>
          <div class="tree-content" :style="{visibility : !isShowResults? 'visible':'hidden' }">
            <el-tree
              :data="data"
              :props="defaultProps"
              node-key="id"
              lazy
              :load="loadNode"
              :expand-on-click-node="false"
              :default-expanded-keys="[orgId]"
              :render-content="renderContent">
            </el-tree>
          </div>
        </div>
        <div class="eim-tree-right">
          <p style="right-desc" v-show="maxNum">已选 ({{selected.length}}/{{maxNum}})</p>
          <p style="right-desc" v-show="!maxNum">已选 ({{selected.length}})</p>
          <div v-for="(select, index) in selected"
          :key="index">{{select.name}}<i @click="removeSelect(select)"></i></div>
        </div>
        <div style="clear:both;"></div>
      </div>
      <div class="tree-footer">
        <el-button @click="clearAll">清空</el-button>
          <el-button type="primary" @click="submit">确 定</el-button>
      </div>
      <div class="errMessage" v-if="errMessage">{{errMessage}}</div>
      <div v-show="isShowloading" class="loading">
        <img :src="require('@/assets/loading.gif')"></div>
    </el-dialog>
  </div>
</template>

