  }
  .eim-tree .el-dialog__headerbtn{
    top: 10px;
  }
  .el-tree-node__expand-icon.el-icon-caret-right:before{
    content: '';
  }
  .eim-tree .el-dialog--tiny {
    width: 578px !important;
  }
  .el-checkbox__inner::after {
    transform: rotate(45deg) scaleY(1);
  }
