    transform: translateY(-50%) rotateZ(180deg) !important;
  }
  .el-scrollbar__bar {
    opacity: 1;
  }
  .eim-tree .el-input-group__append, .eim-tree .el-input-group__prepend {
    color: #333;
    background-color: #fff;
    border: 0px;
  }
  .eim-tree .el-input__inner {
    border: 0px;
  }
  .el-tree-node__expand-icon.expanded {
    transform: rotate(90deg);
  }
  .eim-tree .el-tree-node__expand-icon {
    display: inline-block;
    vertical-align: middle;
    border: 0 none;
    cursor: pointer;
    outline: none;
    background-color: transparent;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-image: url(~@/assets/icon_unfold.png);
    width: 24px;
    height: 24px;
    background-size: cover;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  .eim-tree .is-leaf {
    background-image: none;
  }
</style>
<script>
import ajax from '@/common/vendor/tree/ajax';
import placeholder from '@/common/js/placeholder';

/* eslint-disable */
const sessionTimeoutState = 301;

export default {
  name: 'eim-tree',
  props: {
  },
  created() {
  },
  data() {
    return {
      orgId: '',
      errMessage: '',
      maxNum: 0,
      type: '',
      dialogVisible: false,
      searchInput: '',
      data: [],
      defaultProps: {
        children: 'children',
        label: 'name',
        isLeaf: 'isLeaf',
      },
      orgs: [],
      selected: [],
      isTyping: false,
      _typeTimeout: '',
      searchResults: [],
      searchIndex: 0,
      isShowResults: false,
      isShowSearchSelect: false,
      confirmCallback: '',
      isShowloading: false,
      requserurl: '', // 选人请求地址
      reqorgurl: '', // 选部门请求地址
    };
  },
  watch: {
    searchInput(val, oldVal) {
      if (!val) {
        this.isShowResults = false;
        this.searchResults = [];
        this.searchIndex = 0;
      } else {
        this.search();
      }
    },
  },
  methods: {
    showErrorMsg(errMessage) {
      this.errMessage = errMessage;
      setTimeout(() => {
        this.errMessage = '';
      }, 2000);
    },
    init(type) {
      this.isShowloading = true;
      if (type === 'org') {
        this.getOrgs(this.orgId || 0, (err, orgs) => {
          this.isShowloading = false;
          if (!err) {
            this.data = orgs;
          }
        });
      } else if (type === 'user') {
        const obj = {
          isParent: true,
          orgId: this.orgId || '',
        };
        this.getOrgUsers(obj, (err, orgUsers) => {
          this.isShowloading = false;
          if (!err) {
            this.data = orgUsers.map((orgUser) => {
              return {
                id: orgUser.id,
                name: orgUser.name,
                isNeedCheckbox: !orgUser.isParent,
                isLeaf: !orgUser.isParent,
              };
            });
          }
        });
      }
    },
    show(options) {
      const type = options.type;
      const selected = options.selected;
      this.isShowSearchSelect = false;
      this.searchInput = '';
      this.selected = [];
      this.isShowResults = false;
      this.searchResults = [];
      this.searchIndex = 0;
      this.maxNum = options.maxNum;
      this.requserurl = options.requserurl || '';
      this.reqorgurl = options.reqorgurl || '';
      if (options.confirmCallback) {
        this.confirmCallback = options.confirmCallback;
      }
      if (options.orgId) {
        this.orgId = options.orgId;
      } else {
        this.orgId = '';
      }
      if (options.maxNum) {
        if (typeof options.maxNum === 'number') {
          this.maxNum = options.maxNum;
        } else {
          this.showErrorMsg('最大人员数必须为Number类型');
        }
      }

      if (type !== 'org' && type !== 'user') {
        return this.showErrorMsg('请传入组织树的类型');
      }
      if (typeof selected === 'object') {
        this.selected = selected;
      }
      this.type = type;
      this.init(type);
      this.dialogVisible = true;
    },
    _filterOrg(pId, orgs) {
      return orgs.filter(item => item.pId == pId).map((item) => {
        return {
          id: item.id,
          name: item.name,
          isNeedCheckbox: true,
          pId: item.pId,
        };
      });
    },
    _filterOrgUsers(orgUsers) {
      return orgUsers.map((orgUser) => {
        return {
          id: orgUser.id,
          name: orgUser.name,
          isNeedCheckbox: !orgUser.isParent,
          isLeaf: !orgUser.isParent,
          pId: orgUser.pId,
        };
      });
    },
    getOrgs(pId, callback) {
      if (this.orgs.length > 0) {
        const arr = this._filterOrg(pId, this.orgs);
        return callback && callback(null, arr);
      }
      const obj = {
        type: 1,
        _t: new Date().getTime(),
      };
      ajax({
        type: 'GET',
        timeout: 15000,
        // url: '/uom/org/tree_data',
        url: this.reqorgurl,//生产环境
        data: obj,
        success: (response) => {
          const data = JSON.parse(response);
          if (data && data.state === sessionTimeoutState) {
            return this.showErrorMsg('会话过期，请刷新页面重试');
          }
          this.orgs = data;
          const arr = this._filterOrg(pId, this.orgs);
          callback && callback(null, arr);
        },
        error: (response) => {
          callback && callback('error');
        },
      });
    },
    searchOrg(keyword, callback) {
      const obj = {
        type: 1,
        keywrod: keyword,
        _t: new Date().getTime(),
      };
      ajax({
        type: 'GET',
        timeout: 15000,
        // url: '/uom/org/tree_data',
        url: this.reqorgurl,//生产环境
        data: obj,
        success: (response) => {
          const data = JSON.parse(response);
          if (data && data.state === sessionTimeoutState) {
            return this.showErrorMsg('会话过期，请刷新页面重试');
          }
          callback && callback(null, data);
        },
        error: (response) => {
          callback && callback('error');
        },
      });
    },
    getOrgUsers(obj, callback) {
      obj._t = new Date().getTime();
      ajax({
        timeout: 15000,
        type: obj.isParent ? 'GET' : 'POST',
        url: this.requserurl,
        data: obj,
        success: (response) => {
          const data = JSON.parse(response);
          if (data && data.state === sessionTimeoutState) {
            return this.showErrorMsg('会话过期，请刷新页面重试');
          }
          callback && callback(null, data);
        },
        error: (response) => {
          callback && callback('error');
        },
      });
    },
    searchUser(keyword, callback) {
      const obj = {
        keywrod: keyword, // 服务端接口就是个错误的单词
        _t: new Date().getTime(),
      };
      ajax({
        timeout: 15000,
        type: 'GET',
        url: this.requserurl,
        data: obj,
        success: (response) => {
          const data = JSON.parse(response);
          if (data && data.state === sessionTimeoutState) {
            return this.showErrorMsg('会话过期，请刷新页面重试');
          }
          callback && callback(null, data);
        },
        error: (response) => {
          callback && callback('error');
        },
      });
    },
    changeSearchType(data) {
      this.isShowSearchSelect = false;
      this.orgs = [];
      this.selected = [];
      this.isShowResults = false;
      this.searchInput = '';
      this.init(this.type);
    },
    loadNode(node, resolve) {
      if (node.id === 0) return;
      if (this.type === 'org') {
        this.getOrgs(node.data.id, (err, orgs) => {
          if (!err) {
            const data = this._filterOrg(node.data.id, orgs);
            setTimeout(() => {
              resolve(data);
            }, 300);
          } else {
            resolve([]);
          }
        });
      } else {
        const obj = {
          isParent: false,
          id: node.data.id,
          name: node.label,
          lv: node.data.lv,
        };
        this.getOrgUsers(obj, (err, orgUsers) => {
          if (!err) {
            const data = this._filterOrgUsers(orgUsers);
            setTimeout(() => {
              resolve(data);
            }, 300);
          } else {
            resolve([]);
          }
        });
      }
    },
    removeSelect(select) {
      for (let i = 0; i < this.selected.length; i++) {
        if (this.selected[i].id === select.id) {
          this.selected.splice(i, 1);
        }
      }
    },
    isChecked(data) {
      return !this.selected.every(value => value.id !== data.id);
    },
    check(data) {
      if (this.isChecked(data)) {
        this.removeSelect(data);
      } else {
        if (this.maxNum === 1) {
          this.selected = [];
        }
        if (this.maxNum !== undefined && this.selected.length >= this.maxNum) {
          return this.showErrorMsg('最多只能选择' + this.maxNum + '个数据');
        }
        this.selected.push(data);
      }
    },
    search() {
      this.query(this.searchInput, (data) => {
        this.searchResults = data;
        this.isShowResults = true;
      });
    },
    searchInputClick() {
      if (this.searchResults.length > 0) {
        this.isShowResults = true;
      }
    },
    searchblur($event) {
      this.isShowResults = false;
      this.isShowSearchSelect = false;
    },
    arrowDown(keycode) {
      if (this.searchIndex === this.searchResults.length - 1) {
        return;
      }
      this.searchIndex++;
    },
    arrowUp() {
      if (this.searchIndex === 0) {
        return;
      }
      this.searchIndex--;
    },
    selectSearchResult() {
      if (this.isShowResults && this.searchResults && this.searchResults.length > 0) {
        const data = this.searchResults[this.searchIndex];
        this.handleSelect(data);
      }
    },
    handleSelect(data) {
      this.searchInput = '';
      if (this.maxNum === 1) {
        this.selected = [];
      }
      if (this.maxNum !== undefined && this.selected.length >= this.maxNum) {
        return this.showErrorMsg('最多只能选择' + this.maxNum + '个数据');
      }
      if (!this.isChecked(data)) {
        this.selected.push(data);
      }
      this.isShowResults = false;
      this.searchResults = [];
      this.searchIndex = 0;
    },
    query(queryString, cb) {
      if (this._typeTimeout) {
        clearTimeout(this._typeTimeout);
        this._typeTimeout = '';
      }
      this._typeTimeout = setTimeout(() => {
        this._typeTimeout = '';
        this.querySearchAsync(queryString, cb);
      }, 500);
    },
    querySearchAsync(queryString, cb) {
      queryString = this.searchInput;
      if (!queryString) {
        return cb([]);
      }
      if (this.type === 'org') {
        this.searchOrg(queryString, (err, orgs) => {
          if (!err) {
            const data = orgs.map((item) => {
              return {
                name: item.name,
                value: item.fullName,
                id: item.id,
                pId: item.Pid,
              };
            });
            cb(data);
          } else {
            cb([]);
          }
        });
      } else {
        this.searchUser(queryString, (err, users) => {
          if (!err) {
            const data = users.map((item) => {
              return {
                name: item.name,
                value: item.orgNames,
                id: item.id,
                pId: item.pId,
              };
            });
            cb(data);
          } else {
            cb([]);
          }
        });
      }
    },
    clearAll() {
      this.selected = [];
    },
    submit() {
      this.$emit('tree-confirm', this.selected);
      this.confirmCallback && this.confirmCallback(this.selected);
      this.dialogVisible = false;
      this.selected = [];
    },
    renderContent(h, { node, data, store }) {
      if (data.isNeedCheckbox) {
        return (
          `<div class="treen-node-line" on-click={(event) => { this.check(data); event.stopPropagation(); }}>
            <label class="el-checkbox" style="margin-right:10px;" on-click={(event) => { event.stopPropagation(); }}>
              <span class={{ 'el-checkbox__input': true, 'is-checked': this.isChecked(data) }}>
                <span class="el-checkbox__inner"></span>
                <input type="checkbox" class="el-checkbox__original" on-change={() => { this.check(data); }}/>
              </span>
            </label>
            <span>
              <span>{node.label}</span>
            </span>
          </div>`
        );
      }
      return (
        `<div class="treen-node-line">
          <span>
            <span>{node.label}</span>
          </span>
        </div>`
      );
    },
  },
  mounted() {
    placeholder();
  }
};
</script>



