
//积分排行页面

<template>
  <div  class="c_content_g c_content">
    <el-tabs  class="c_el_tabs" size="mini" v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="全部排名" name="first"></el-tab-pane>
      <el-tab-pane label="直属单位排名" name="second"></el-tab-pane>
      <el-tab-pane label=" 部门积分" name="third"></el-tab-pane>
    </el-tabs>


    <div class="c_content_top">

      <span>时间段</span>
      <el-date-picker
        v-model="date_value"
        class="c_el-date-picker"
        type="daterange"
        size="mini"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>

      <div class="c_content_top_right">
      <span  class="c_score_span">总分：</span>
      <span class="c_score_warn_span">80</span>
      <span  class="c_rank_span">排名：</span>
      <span class="c_rank_warn_span">25</span>

