          prop="date"
          label="主题"
          width="400">
        </el-table-column>
        <el-table-column class="c_el-table-column"
                         prop="name"
                         label="稿件分类"
        >
        </el-table-column>
        <el-table-column class="c_el-table-column"
                         prop="address"
                         label="栏目">
        </el-table-column>

        <el-table-column class="c_el-table-column"
                         prop="address"
                         label="采纳时间">
        </el-table-column>
      </el-table>

    </div>


    <el-pagination

      class="c_pagination"
      layout="prev, pager, next"
      :total="1000">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'score_rank',
  data() {
    return {
      activeName: 'first',
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
  },
};
</script>

<style scoped>


  .c_content {

    padding: 8px 20px 10px 12px;

  }


  .c_content_top {
    background: #F5F7FA;
    border-radius: 2px;
    padding: 8px 10px;
    align-items:center;
  }

  .c_content_top_right{

    display: inline-block;
    float: right;
    margin-top: 6px;


  }

  .c_el-date-picker {

    width: 244px !important;
    margin-left: 1%;
    margin-right: 3%;
  }

  .c_el_tabs{

  }

  .c_score_span{

    font-size: 14px;
    color: #606266;
    letter-spacing: 0;
    line-height: 14px;
  }


  .c_score_warn_span{

    font-size: 16px;
    color: #FF7D00;
    letter-spacing: 0;
    line-height: 14px;

  }

  .c_rank_span{

    font-size: 14px;
    color: #606266;
    letter-spacing: 0;
    line-height: 14px;
    margin-left: 48px;

  }

  .c_rank_warn_span{

    font-size: 16px;
    color: #FF7D00;
    letter-spacing: 0;
    line-height: 14px;

  }


  .c_table_base {

    border: 1px solid lightgray;
  }

  .c_table_info {

    margin-top: 12px;
  }

  .c_pagination {
    position: absolute;
    right: 20px;
    margin-top: 30px;
  }

  .el-pagination {

    color: #606266 !important;
  }


</style>



