/* eslint-disable */
export default function placeholder() {
// 仅在不支持 placeholder 的时候执行
  if (!('placeholder' in document.createElement('input'))) {
    let listenerName = 'attachEvent';
    let listenerPrefix = 'on';
    if ('addEventListener' in window) {
      listenerName = 'addEventListener';
      listenerPrefix = '';
    }

    const placeholderListener = {
      // 添加输入项
      add(tagName) {
        const list = document.getElementsByTagName(tagName);
            for (let i = 0; i < list.length; i++) {
                this.render(list[i]);
            }
            return this;
        },
        //渲染
        render(dom) {
            let text = dom.getAttribute('placeholder');
            if (!!text) {
                this.attachEvent(dom, this.getDiv(dom, text));
            }
        },
        //设置样式
        getDiv (dom, text) {
            let div;
            let parentNode = dom.parentNode;  // 父节点
            let random =  Math.floor(Math.random() * 100000000000);  // 唯一标别

            let domPlaceholderId =  dom.getAttribute('inputId');

            let aDivs = parentNode.getElementsByTagName('div');
            for (let i=0; i< aDivs.length; i++) {
                let divAttr = aDivs[i].getAttribute('placeholderId');
                if(domPlaceholderId === divAttr) {
                    div = aDivs[i];
                }
            }

            if (!domPlaceholderId) { //不存在再添加
                div = document.createElement('div');
                dom.setAttribute('inputId', random);
                div.setAttribute('placeholderId', random);
                div.style.position = 'absolute';
                div.style.width = this.getPosition(dom, 'width');
                div.style.lineHeight = this.getPosition(dom, 'lineHeight');
                div.style.height = this.getPosition(dom, 'height');
                div.style.left = 0;
                div.style.top = 0;
                div.style.color = '#A8ABB3';
                div.style.paddingLeft = this.getPosition(dom, 'paddingLeft');
                div.style.paddingTop = this.getPosition(dom, 'paddingTop');
                div.style.paddingBottom = this.getPosition(dom, 'paddingBottom');
                div.style.zIndex = 999;
                div.style.background = dom.style.background;
                div.style.border = dom.style.border;
                div.style.cursor = 'text';
                div.innerHTML = text;
                parentNode.insertBefore(div, dom);
            }
            
            return div;
        },
        // 计算当前输入项目的位置
        getPosition (dom, name, parentDepth = 0) {
            let offsetName = 'offset' + name.charAt(0).toUpperCase() + name.slice(1);
            let offsetVal = dom.currentStyle[offsetName] || dom.currentStyle[name];
            // let offsetVal = getComputedStyle(dom)[offsetName] || getComputedStyle(dom)[name];  // 现代浏览器
            // let offsetVal = dom[offsetName];
            if (!offsetVal && parentDepth < 3) {
                offsetVal = this.getPosition(dom.parentNode, name, ++parentDepth);
            }
            return offsetVal;
        },
        // 添加事件
        attachEvent (dom, div) {
            if (!dom && !div) return;
            const that = this;
            setTimeout(function () {
                that.nullValue(dom, div);
            }, 30);
            // 激活时，隐藏 placeholder
            dom[listenerName](listenerPrefix + 'focus', function () {
                if( dom.value.length>0) {
                    div.style.display = 'none';
                }
            });

            // 失去焦点时，隐藏 placeholder
            dom[listenerName](listenerPrefix + 'blur', function (e) {
                if (e.srcElement.value == '') {
                    div.style.display = '';
                }
            });

            // placeholder 点击时，对应的输入框激活
            div[listenerName](listenerPrefix + 'click', function (e) {
                e.srcElement.style.display = 'none';
                dom.focus();
            });

            div[listenerName](listenerPrefix + 'change', function (e) {
                e.srcElement.style.display = 'none';
            });
        },
        nullValue(dom, div) {
            if (dom.value.length > 0) {
                div.style.display = 'none';
            }
        },
    };
    //防止在 respond.min.js和html5shiv.min.js之前执行
    setTimeout(function () {
        placeholderListener.add('input').add('textarea');
    }, 30);
}
}


