import Vue from 'vue';
import Router from 'vue-router';
import MainFrame from '@/views/main_frame';
import Home from '@/views/home';
import SendMsg from '@/views/send_msg';
import ShareMsg from '@/views/share_msg';
import ScoreRank from '@/views/score_rank';
import Rules from '@/views/rules_regulations';
import Publish from '@/views/publish';
import InfoDetail from '@/views/info_detail'
import NewInfo from '@/views/new_info'
import UserInfo from '@/views/user_info'


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'submitMsg',
      component: MainFrame,
      redirect: 'home',
      children: [
        {

          name: 'home',
          path: 'home',
          component: Home,
        },
        {

          name: 'sendMsg',
          path: 'sendMsg',
          component: SendMsg,
        },
        {

          name: 'shareMsg',
          path: 'shareMsg',
          component: ShareMsg,
        }, {

          name: 'scoreRank',
          path: 'scoreRank',
          component: ScoreRank,
        }, {

          name: 'rules',
          path: 'rules',
          component: Rules,
        }, {

          name: 'publish',
          path: 'publish',
          component: Publish,
        },

      ],



    },{

      path: '/infoDetail',
      name: 'infoDetail',
      component: InfoDetail,

    },{

      path: '/newInfo',
      name: 'newInfo',
      component: NewInfo,

    },{

      path: '/userInfo',
      name: 'userInfo',
      component: UserInfo,

    }


  ],
});



