<template>
  <div class="c_content_g c_content">

    <div class="c_left">
      <div>
        <i class="iconfont icon-info_icon  c_i_info"></i><span class="c_header_text">信息报送</span>
      </div>

      <div>
        <el-table class="c_table_base c_table_info"
                  :data="infoTableData"
                  @row-click="onInfoTableRowClick"

        >
          <el-table-column
            prop="title"
            label="主题"
            min-width="40%"
          >
          </el-table-column>
          <el-table-column class="c_el-table-column"
                           prop="draftTypeStr"
                           label="稿件分类"
                           min-width="10%"
          >
          </el-table-column>
          <el-table-column class="c_el-table-column"
                           prop="draftColumnStr"
                           min-width="10%"
                           label="栏目">
          </el-table-column>

          <el-table-column
            prop="statusStr"
            label="状态"
            min-width="10%"

          >
            <template slot-scope="scope">
              <el-tag

                size="mini"
                :type="stausBackguand(scope.row.status)"
                disable-transitions>{{scope.row.statusStr}}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column class="c_el-table-column"
                           prop="createTime"
                           min-width="10%"
                           :formatter='updateTimeFormat'
                           label="提交时间">
          </el-table-column>
        </el-table>


      </div>


      <div class="c_div_rules">
        <i class="iconfont icon-institution_icon c_i_rules"></i>
        <span class="c_header_text">规章制度</span>
      </div>

      <el-table class="c_table_base c_table_rules"
                :data="rulesTableData"
                :show-header=false
                @row-click="onRulesTableRowClick"

                style="width: 100%">
        <el-table-column
                         prop="policyName"
                         label="主题"

        >
        </el-table-column>

      </el-table>

    </div>
    <div class="c_right">
      <div>
        <i class="iconfont icon-rankings_icon c_i_rank"></i><span class="c_header_text">积分排行</span>
      </div>

      <ScoreRank class="c_score_rank"></ScoreRank>


    </div>
  </div>
</template>
<script>

import Api from '@/api/api';
import ScoreRank from '../components/score-rank';

export default {
  name: 'home',

  components: {

    ScoreRank,

  },

  data() {
    return {
      infoTableData: [],
      rulesTableData: [],
    };
  },
  methods: {
    // 获取table数据
    getInfoData(param) {
      if (typeof param !== 'object') {
        param = {};
      }

      if (!param.hasOwnProperty('pageSize')) {
        param.pageSize = '10';
      }

      Api.getApi('get', 'pageQueryAlreadySubmitInfo', param, (data) => {
        console.log(data);

        this.infoTableData = data.records;
      }, (data) => {
        console.log(data);
      });
    },
    updateTimeFormat(row, column, cellValue) {
      if (typeof cellValue === 'string') {
        return cellValue.split(' ')[0];
      }
      return '';
    },
    stausBackguand(status) {
      let result;
      switch (status) {
        case 4:
          result = 'primary';
          break;
        case 2:
          result = 'success';
          break;
        case 3:
          result = 'danger';
          break;
        default:
          break;
      }
      return result;
    },
    // 获取规章table数据
    getRulesData(param) {
      let temp = param;
      if (typeof temp !== 'object') {
        temp = {};
      }

      if (!temp.hasOwnProperty('pageSize')) {
        temp.pageSize = '10';
      }

      Api.getApi('get', 'queryRolePolicy', temp, (data) => {
        console.log(data);

        this.rulesTableData = data.records;
      }, (data) => {
        console.log(data);
      });
    },


    onRulesTableRowClick(row) {
      this.$router.push({
        name: 'infoDetail',
        params: {
          title: row.policyName,
          draftContent: row.content,
          updateTime: row.releaseTime,
        },
      });
    },
    onInfoTableRowClick(row) {
      this.$router.push({
        name: 'infoDetail',
        params: row,
      });
    },


  },

  mounted() {
    this.getInfoData({});
    this.getRulesData({});
  },
};
</script>


<style scoped>


  .c_content {

    padding: 20px;
  }

  .c_left {
    height: 100%;
    position: absolute;
    right: 250px;
    left: 20px;

  }

  .c_right {

    height: 100%;
    margin-left: 20px;
    position: absolute;
    right: 20px;

  }

  .c_i_info {

    margin-right: 5px;
    color: #0191e5;

  }

  .c_i_rules {

    margin-right: 5px;
    color: #29bf6c;

  }

  .c_i_rank {

    margin-right: 5px;
    color: #fa785d;

  }

  .c_header_text {
    font-size: 14px;
    color: #737B8A;
    letter-spacing: 0;
    line-height: 14px;
  }

  .c_table_base {

    border: 1px solid lightgray;
  }

  .c_table_info {

    margin-top: 12px;
  }

  .c_table_rules {

    margin-top: 12px;

  }

  .c_div_rules {
    margin-top: 52px;
  }

  .c_score_rank {
    margin-top: 12px;
  }

  .c_table_info th {

    background-color: #EBEEF6 !important;
  }


</style>



