import Vue from "vue";
import axios from "axios";
import { Message } from "element-ui";
import state from "./state";

Vue.prototype.$ELEMENT = { size: "small" };

const prefix = "/infosubmitted/api";
// api列表
const apiList = {

  //分页查询已共享信息报送
  pageQueryAlreadyShare: prefix + "/pageQueryAlreadyShare",
  //查询稿件类型
  queryDraftType: prefix + "/queryDraftType",
  //查询稿件栏目
  queryDraftColumn: prefix + "/queryDraftColumn",
  //分页查询信息报送
  pageQueryAlreadySubmitInfo: prefix + "/pageQueryAlreadySubmitInfo",
  //根据条件分页查询规则制度
  queryRolePolicy:prefix+"/queryRolePolicy"


};


// axios.defaults.timeout = 15000;

axios.create({
  timeout: 10000,
});

const headers = {};

const responseHandler = (response) => {

  if (response.data.state === 301) {
    setTimeout(() => {
      location.href = '/infosubmitted/login/redirect/management/index.html'
    });
  }else  if (response.data.state !== 0) {
    console.error('ajax response error', response.data.message, state[response.data.state]);
    return '';
  }
  return response;
};

const errorHandler = (error) => {
  if (error.message !== 'repeat') {
    console.error('ajax response error', error);
    Message.error(i18n.t('error.apiError'));
  }
  return Promise.reject(error);
};

const req = {};
const requestHandler = (config) => {
  if (config.abort) {
    config.cancelToken = new Axios.CancelToken((c) => {
      const { url } = config;
      if (req[url]) {
        req[url]('repeat');
        delete req[url];
      }
      req[url] = c;
    });
  }
  config.params = config.params || {};
  config.params.t = (new Date()).valueOf();
  config.headers = Object.assign({}, config.headers, headers);
  if (headers.Authorization && /MSIE 9/i.test(navigator.appVersion)) {
    config.params.Authorization = headers.Authorization;
  }
  return config;
};


axios.interceptors.request.use(requestHandler);
axios.interceptors.response.use(responseHandler, errorHandler);



// 全局ajax请求
const getApi = (type, url, params, callback, errCallback) => {


  if (type === "get") {
    params.t = new Date().getTime(); // 带时间戳，阻止缓存
    return axios.get(apiList[url], {
      params
    }).then((response) => {
      if (response.data.state === 0) {
        if (typeof callback === "function") {
          callback(response.data.data, response.data.message);
        }
      } else if (response.data.state === 301) {
        setTimeout(() => {
          location.href = '/infosubmitted/login/redirect/management/index.html'
        });
      } else if (typeof errCallback === "function") {
        errCallback(response.data);
      }
    }).catch(() => {
      Message.error("系统异常");
    });
  }
  if (type === "post") {
    return axios.post(apiList[url], params).then((response) => {
      if (response.data.state === 0) {
        if (typeof callback === "function") {
          callback(response.data.data, response.data.message);
        }
      } else if (response.data.state === 301) {
        setTimeout(() => {
          location.href = '/infosubmitted/login/redirect/management/index.html'
        });
      } else if (typeof errCallback === "function") {
        errCallback(response.data);
      }
    }).catch(() => {
      Message.error("系统异常");
    });
  }
  return "error";
};


//session 续期
(()=>{

  setInterval(() => {

    axios.get(apiList['queryDraftColumn']);
    console.log("====== extension session =========> ");

  },60*1000)

})();


//获取稿件分类
const getClassficData = function(callback)
  {

    getApi("get", "queryDraftType", {}, callback, (data) => {

    console.log(data);

  });

}

//获取稿件栏目
const  getColumnData = function(callback){

  getApi("get", "queryDraftColumn", {}, callback, (data) => {

    console.log(data);

  });

}

export default {getApi,getColumnData,getClassficData};



