<template>


  <div class="c_tab">


    <ul >

      <li v-for="(tabItem,tabIndex) in itemLists" :key="tabIndex" class="c_tab_item"
          :class="{c_tab_text_select:tabIndex === currentTabIndex}"
          @click="onTabItemClick(tabItem,tabIndex)">

        <div>

          <i :class="tabItem.icon" class="iconfont c_tab_icon_normal"></i>
          <span class="c_tab_text_normal">{{tabItem.text}}</span>

        </div>

      </li>

    </ul>


  </div>


</template>

<script type="text/ecmascript-6">


export default {
  name: 'main-tab',
  props: ['itemLists'],

  data() {
    return {

      currentTabIndex: 0,
    };
  },
  methods: {

    onTabItemClick(tabItem, tabIndex) {
      this.$router.replace({
        name: tabItem.pageName,
      });

      this.currentTabIndex = tabIndex;
    },


  },
  mounted() {

  },
  created() {
    // this.$router.replace({
    //   name: this.itemLists[this.currentTabIndex].pageName,
    // });
  },

};
</script>

<style scoped>


  .c_tab {

    height: 60px;
  }

  .c_tab ul {

    -webkit-padding-start: 0px;
    height: 100%;

  }

  .c_tab_item {

    width: 100px;
    height: 100%;
    float: left;
    list-style: none;
    text-align: center;

  }

  .c_tab_item:hover{
    cursor:pointer;
  }

  .c_tab_icon_normal {

    color: rgba(255, 255, 255, 0.6);
   font-size: 28px;
    display: inline-block;
    margin-top: 5px;

  }


  .c_tab_text_normal {

    display: block;
    color: rgba(255, 255, 255, 0.6);
    text-align: center;
    margin-bottom: 8px;
    margin-top: 3px;
    font-size: 13px;

  }

  .c_tab_text_select {
    background-color: #1d7be6;

  }

  .c_tab_text_select i {
    color: rgba(255, 255, 255, 1);

  }

  .c_tab_text_select span {
    color: rgba(255, 255, 255, 1);
  }

</style>



